*{
    font-family: Arial, Helvetica, sans-serif;

}

body{
    padding: 60px;
}

input{
    color: rgb(13, 13, 113);
    font-size: 1.2 rem;
    width: 100%;
}

.Card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
}


.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}


.card p {
    padding: 5px;
    text-align: center;

}

c.card img {
    max-height: 200px;
}

